import React, { useState,useEffect } from 'react';
import { useHistory,useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';


/* styled-css */
let Box = styled.div`
    padding : 20px;
`;
let Title = styled.h4`
    font-size : 25px;
    color : ${ props => props.color };
    font-weight : ${ props => props.fontW };
`;


class Detail2 extends React.Component {

    componentDidMount(){
    
    }
    //생겨날때 코드 실행
    //ajax요청등..

    componentWillUnmount(){

    }
    //사라질때 코드실행

} //과거 문법

function Detail(props){
    let [container, setContainer] = useState();
    
    //여러개면 적힌 순서대로 실행
   /*  useEffect(()=>{
        alert("내가 먼저다 이자식들아")
        setTimeout(function(){
            alert("di")
        },2000)
        return function 머머머(){실행할 코드} 이건 사라질때 실행하는 코드
    }); */

    let [timer,timerChg] = useState(true);
    let [text,textChg] = useState('');

    useEffect(()=>{
        let timer = setTimeout(()=>{ timerChg(false) },2000)
        //return function 머머머(){실행할 코드} //이건 사라질때 실행하는 코드

        return ()=>{ clearTimeout( timer ) }//버그가 생길 수 있으니 페이지에서 나갈때는 지워주는게 좋음

        console.log('안녕')
    },[]);/* timer, text  //timer라는 state가 변경 업데이트 될때만 실행해 주세요. */ /* 조건안에 아무것도 없으면 페이지가 로드가 됐을때만 실행됨*/


    let { id } = useParams();
    let history = useHistory();

    let filter = props.shoes.find(function(shoe){
        return shoe.id == id;
    })

    function chgPro(){
        let newA = [...props.left]
        newA[0] = newA[0] - 1;
        props.chgLeft(newA)
    }


    return(
        <div className="container">
            <Box>
                {/* <Title color='red' fontW="bold" className="red">상세페이지</Title> */}
                <Title color={'black'} fontW="bold" >Detail</Title>

                { text }

                <input onChange={ (e)=>{ textChg(e.target.value) } } />
                
                {
                    timer == true 
                    ?  (<div className="my-alert2">
                            <p>재고가 얼마 남지 않았습니다</p>
                        </div>)
                    : null 
                }
               
            </Box>
            <div className="row">
                <div className="col-md-6">
                    <img src={"https://codingapple1.github.io/shop/shoes"+[ filter.id+1 ] +".jpg"} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                     <h4 className="pt-5">{ filter.title }</h4>
                    <p>{ filter.content }</p>
                    <p>{ filter.price } 원</p>
                    <Left left={props.left}></Left>

                    <button className="btn btn-danger" onClick={ chgPro }>주문하기</button> 
                    <button className="btn btn-success" onClick={()=>{
                         history.goBack();
                         /* history.push('/') *///특정경로로 이동시켜주세요
                         /* history.push('/ddddddadad') */
                         } }>뒤로가기</button> 
                </div>
            </div>
        </div>
    )
}

function Left(props){
    return(
        <p>재고 : { props.left[0] } </p>
    );
}

export default Detail;